<template>
    <div>
        <button>123</button>
        <div class="calendar"></div>
    </div>
</template>
<script>
import $ from "jquery";
export default {
    data() {
        return {
            objdata: {}
        }
    },
    methods: {
        calendar() {

        }

    },
    mounted() {
    }
}
</script>
<style  lang='less' rel='stylesheet/less'>
.calendar {
    display: none;
    position: fixed;
    width: 100%;
    z-index: 999999999;
    top: 0;
    overflow: auto;
    height: 100%;
    background: #fff;
}

.firstSelect {
    width: 100%;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    overflow: scroll;
    display: none;
}

body td {
    text-align: center;
    height: 40px;
}

.dateZone {
    width: 99.4%;
    margin: auto;
    background: #f4f3f8;
    border-bottom: 1px solid #ddd;
    color: #666;
}

.dateZone td {
    background: #f4f3f8;
}

.tbody td {
    background: #fff;
}

.dateZone td {
    width: 14.2%;
}

.dateZone .colo {
    color: #09F;
}

.dateTable {
    width: 99.4%;
    margin: auto;
    border-bottom: 1px solid #ddd;
}

.tbody .ny1 {
    width: 100%;
    text-align: center;
    padding: 5px 0;
}

.dateTable td {
    width: 14.2%;
}

.dateTable .rz {
    font-size: 14px;
}

.datTip {
    background: rgba(0, 153, 255, 0.8);
    width: 40%;
    position: fixed;
    bottom: 2px;
    left: 30%;
}

.datTip p {
    width: 100%;
    margin: auto;
    text-align: center;
    color: #fff;
    padding: 3px 0;
    font-size: 14px;
}

.rz {
    position: relative;
    /* display: none; */
}

.hover {
    font-size: 14px;
    display: inline-block;
    width: 60%;
    background: #FF3;
    /* display: none; */
    color: red;
    text-align: center;
    border-radius: 5px;
}

td {
    border-radius: 2px;
}

section {
    margin: 10px;
    padding: 5px;
}

@media only screen and (min-width:400px) {
    body td {
        text-align: center;
        height: 47px;
    }
}

@media only screen and (max-width:320px) {
    body {
        font-size: 14px;
    }
    .dateTable .rz {
        font-size: 12px;
    }
    .datTip p {
        font-size: 12px;
    }
    .hover {
        font-size: 12px;
    }
    body td {
        text-align: center;
        height: 35px;
    }
}
</style>



